*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App, .App-header, .App-body, form, .App-weather-info, .App-weather-info p{
  display: flex;
}

.App{
 background-image: linear-gradient(140deg, #c7b92d, #e8775b);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  font-family: 'Alata', sans-serif;
  color:  #0c71e0;
}

.App-header{
  align-items: center;
  justify-content: center;
}

.App-logo{
    width: 80px;
    height: 80px;
}

.App-header h1{
  font-size: 18px;
}

.App-body{
  flex-direction:column;
  align-items: center;
  width: 90%;
  height: 30vh;
}

form{
justify-content: space-around;
min-width: 270px;
}

input{
  width: 150px;
  height: 25px;
  border: none;
}

input:focus{
  box-shadow: none;
}

button{
  width: 80px;
  height: 25px;
  border-radius: 12.5px;
  border:none;
  background-color: #0c71e0;
  color: azure;
}

p{
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.App-weather-info{
  align-items: center;
  justify-content: space-evenly;
  margin-top: 30px;
}

.App-weather-icon{
    width: 25px;
    height: 25px;
    margin-left: 8px;
    margin-right: 10px;
}

.App-weather-icon-humidity{
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

h4{
  text-align: center;
  margin-top: 50px;
}

.App-footer{
  width: 90%;
  height: 10vh;
  position: absolute;
  left: auto;
  bottom: 0;
}


div.App-footer p{
  font-size: 12px;
  font-style: italic;
  text-align: center;
}

@media only screen and (min-width: 768px){
  .App-logo{
    width: 200px;
    height: 200px;
  }

  .App-header h1{
    font-size: 48px;
  } 

  form{
    width: 600px;
  }

  input{
    width: 300px;
    height: 40px;
  }

  button{
    height: 40px;
    border-radius:20px;
    width: 140px;
  }

  h4{
    font-size: 26px;
  }

  div.App-footer p{
    font-size: 18px;
  }

  
.App-weather-icon{
  width: 40px;
  height: 40px;
  margin-left: 8px;
  margin-right: 10px;
}

.App-weather-icon-humidity{
  width: 40px;
  height: 40px;
  margin-right: 15px;
}

p{
  font-size: 25px;
}

}